import React from 'react';
import logo from './drops.jpeg';
import {Link} from 'react-router-dom';
import './estilo_biblioteca.css';
import logo_drops from './logo_drops.png';

export default class App extends React.Component{
    constructor(props){
        super(props)
    }
    render(){
        return(
            <div className = 'App' style ={{backgroundColor:'black'}}>
                <header className = 'App-header'>
                    <div class = 'logodrops'>
                    <Link to='/' class ='link'><button class = 'imagem'>
            <img src={logo} class = 'imagem'></img>
          </button></Link>
              </div>
            <div class = 'ceu'>
                <div class = 'covid'>
                <img src = {logo_drops} style = {{width:100,height:100,}}/>
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </div>
            <div class = 'covid2'>
                <div class = 'box'></div>
            </div>
                </header>
            </div>
        )
    }
}