import {Routes,Route} from 'react-router-dom';
import Pagina1 from '../PaginaInicial/Pagina_Inicial';
import Sobre from '../Sobre/Sobre';
import React from 'react';
import Mascaras from '../Mascaras/Mascaras';
import Distanciamento from '../Distanciamento/Distanciamento';
import Noticias from '../Biblioteca/Biblioteca';
import Instalar from '../Instalar_App/instalar';
import Testes from '../Testes/Test';
import Estrelas from '../stars/estrelas';
import Dist from '../dist/distan';
import Dados from '../Dados_Covid/Dados';
import Redes from '../Redes/rede';
import Inicio from '../Inicio/inicio';
import Confiaveis from  '../Confiaveis/conf';

export default function App(){
    return(
        <Routes>
            <Route path = '/' element = {<Pagina1/>}></Route>
            <Route path = '/Sobre' element = {<Sobre/>}></Route>
            <Route path = '/Mascaras' element = {<Mascaras></Mascaras>}></Route>
            <Route path = '/Distanciamento' element = {<Distanciamento></Distanciamento>}></Route>
            <Route path = '/Noticias' element = {<Noticias></Noticias>}></Route>
            <Route path = '/Instalar_o_App' element = {<Instalar></Instalar>}></Route>
            <Route path = '/testes' element = {<Testes></Testes>}></Route>
            <Route path = '/estrelas' element = {<Estrelas></Estrelas>}></Route>
            <Route path = '/dist' element = {<Dist></Dist>}></Route>
            <Route path = '/Dados' element = {<Dados></Dados>}></Route>
            <Route path = '/Inicio' element = {<Inicio></Inicio>}></Route>
            <Route path = '/Confiaveis' element = {<Confiaveis/>}></Route>
            </Routes>
    );
}