import React, { Component } from 'react';
import './inicial_css.css';
import background from './covid_stars.png';
import planeta from './planeta.png';

export default class App extends React.Component{
    render(){
        return(
            <div class = 'main_js'>
            <div class = 'estilo_iniciojs'>
                <div class = 'app_iniciojs'></div>
                <div class = 'logo_iniciojs'></div>
                <div class = 'banner_iniciojs'>
                <img src = {background} class = 'img_banner_iniciojs'></img>
                <img src = {planeta} class = 'img_banner_planetajs'></img>
                </div>
                <div class = 'banner_redirecionamentojs'></div>
                <div class = 'catalogo_js'></div>
            </div>
            </div>
        );
    }
}