import React from 'react';
import logo from './drops.jpeg';
import './estilo_mascaras.css';
import Carousel from 'react-elastic-carousel';
import {Link} from 'react-router-dom';

export default class App extends React.Component{
  constructor(props){
    super(props)
    this.state={
      nome:'Bem vindo ao tópico sobre Máscaras',
      color:'white',
      frase: 'ss',
      imagens:[
        {titulo:'',id:'\u2022Questão prévia: Tu sabe o que danado segura o vírus (o corona) na máscara?',url:'',imgs:''},
        {titulo:'',id:'Você já viu uma máscara bem de pertinho?\n E aí, bora ver? Primeiro, pegue algum retalho de tecido não tecido (TNT) simples (pergunta a mainha se ela não tem alguma embalagem que seja de TNT, aquelas embalagens de presente, sacolinhas ouuuu aqueles panos multiusos de tirar pó). Depois, é só pegar um celular (o seu ou o de mainha) e abrir na câmera, aproxime beeeeem de pertinho do TNT.',url:'',imgs:'ss'},
        {titulo:'',id:'\u2022Pergunta inicial: O que há na máscara? Você conseguiu ver? Descreva pra gente! Você acha que o TNT consegue segurar algo tão pequeno como o coronga? ',url:'',imgs:''},
        {titulo:'',id:'Agora, vamos ver algo legal, tá? Olha só! Olha lá! Faz com a gente este experimento! Tu vai precisar desses materiais aí:',url:'',imgs:''},
        {titulo:'',id:'\u2022Materiais:\n\n\u2022Isopor ou papel (as folhas do seu caderno, nada de folha de ofício, tá baum?);\n\u2022Máscara de tecido não tecido (TNT) (máscara cirúrgica, aquela que os(as) médicos(as) usam);\n\u2022Recipiente, pote, prato (De preferência que seja raso);\n\u2022Retalho de TNT simples;\n\u2022Luva (pergunta à mainha se ela tem aquelas luvas de plástico que vem na caixinha de tinta para pintar o cabelo).',url:'',imgs:''},
        {titulo:'',id:'\u2022Realize a experiência em um dia seco.',url:'',imgs:'s'},
        {titulo:'',id:'\u2022Olha a máscara aí minha gente',url:'',imgs:'s'},
        {titulo:'',id:'\u2022Olha a máscara aí minha gente',url:'',imgs:'s'},
      ]
    }
  }
  mostrar(){
    if(document.documentElement.scrollTop>100){
      this.setState({
        nome:'Você está lendo: Tipos de máscaras!',
        color:'red',})}
    else if(document.documentElement.scrollTop==0){
      this.setState({
        nome:'Bem vindo ao tópico sobre Máscaras',
        color:'white',
      })}}
  componentDidMount(){
    window.onscroll = () => this.mostrar()}
render(){
return(
    <div className="App">
        <header className="App-header">
        <div class = 'logodrops'>
        <Link to='/' class ='link'><button class = 'imagem'>
            <img src={logo} class = 'imagem'></img>
          </button></Link>
        </div>
        <div class = 'espaco'>Máscaras</div>
        <div class = 'espacamento'>
          <Carousel
          pagination = {true}
          autoPlaySpeed = {1000}
          initialActiveIndex = {true}
          enableAutoPlay={false}>
            {this.state.imagens.map(item=>
              <div class = 'slides' key = {this.state.imagens}>
              <div class = 'text'>{item.id}</div>
              <div class = {(item.imgs == '')?'':'imgs'}></div></div>
              )}
          </Carousel></div>
        <div class = 'banner_preto'>
        </div>
        </header>
    </div>
)}}