import React from 'react';
import './confia.css';
import logo from './logo_drops.png';
import {Link} from 'react-router-dom';
export default class App extends React.Component{
render(){
    return(
        <div class = 'confiaveis_conf' >
            <header class = 'header_conf'>
                <button class = 'botao_logo_conf'>
                    <Link to = '/' class = 'imagem_logo_conf'>
                        <img class = 'imagem_logo_conf' src={logo}></img>
                    </Link>
                </button>
            </header>
            <div class = 'titulo_conf'>Sites Confiáveis</div>
            <div class = 'public_conf'></div>
        </div>
        );
}}