import React from 'react';
import logo from './logo_drops.png';
import './estilo_sobre.css';
import {Link} from 'react-router-dom';
import Anailza from './Anailza.png';
import Cesar from './Cesar.png';
import Daisy from './Daisy.png';
import Gabriella from './Gabriella.png';
import Italo from './Italo.png';
import Jessica from './Jessica.png';
import Lucio from './Lucio.png';
import Marcilio from './Marcilio.png';
import Rodrigo from './Rodrigo.png';
import Thalita from './Thalita.png';
import Fabiana from './Fabiana.png';
import Vamberto from './Vamberto.png';
import Levi from './Levi.png';
import FabiPensando from './Banner_Fabi.png';
import thal from './Banner2.png';
import deisyy from './deisyy.png';
import JessPensando from './BannerJ.png';

export default class App extends React.Component{
constructor(props){
  super(props)
  this.state = {
    texto:'\n\nEm Escolas Públicas da região de Campina Grande, observamos que um grande número de professores(as) de Física, não utilizavam de atividades experimentais em suas aulas, mesmo essa ciência sendo de caráter totalmente manipulativo [experimental].\n\nMuitos professores(as) têm em suas respostas para tal problema a simples argumentativa, plausível, que o tempo e material necessário para construção e preparação de aulas experimentais são escassos, o que prejudica a aplicação dessas atividades, especialmente em escolas da Rede Pública [Estadual].\n\nMuitas dessas escolas não possuem kits experimentais, que viabilizem à aplicação de aulas práticas das ciências exatas, em especial a Física e, quando possuem, os professores(as) não se sentem a vontade pra realizar essa prática, seja por falta de tempo ou habilidade com essas ferramentas.\n\n\n',
    texto2:'\n\nO DROPS de Física foi uma iniciativa dos recém graduandos do curso de Física, 2016, que vivenciaram pouca, ou nenhuma, interação experimental em suas aulas da disciplina,  junto a uma professora do nosso departamento.\n\nA ideia do projeto é viabilizar essa interação ALUNO(A) – MUNDO MATERIAL [experimento] em pequenas intervenções, aplicada por um grupo de licenciandos(as), abstendo o(a) professor(a) de qualquer intervenção na atividade experimental. Caso ele(a) queira, pode criar uma sequência, anterior ou posterior a nossa atividade.\n\n\n',
    texto3:'\n\n\nAo escolher, adequamos o material a atividade prevista, marcamos a data de intervenção, e aplicamos a atividade. Nosso grupo disponibiliza material (seja a montagem experimental, roteiros, e uma equipe para aplicar). Durante a aplicação, ainda, possibilitamos uma discussão sobre o assunto abordado.',
    pessoas:[
    {nome:'Daisy',funcao:'Coordenadora',foto:Daisy},
    {nome:'Levi',funcao:'Desenvolvedor',foto:Levi},
    {nome:'Gabriella',funcao:'Desenvolvedora',foto:Gabriella},
    {nome:'Jéssica',funcao:'Redatora',foto:Jessica},
    {nome:'Thalita',funcao:'Desenvolvedora',foto:Thalita},
    {nome:'Vamberto',funcao:'Redator',foto:Vamberto},
    {nome:'Rodrigo',funcao:'Redator',foto:Rodrigo},

  ],
  pessoas2:[
    {nome:'Anailza',funcao:'Redatora',foto:Anailza},
    {nome:'Marcílio',funcao:'Redator',foto:Marcilio},
    {nome:'Lúcio',funcao:'Redator',foto:Lucio},
    {nome:'Italo',funcao:'Desenvolvedor',foto:Italo},
    {nome:'Fabiana',funcao:'Redatora',foto:Fabiana},
    {nome:'César',funcao:'Redator',foto:Cesar},
  ]
  }
}

render(){
    return(
        <div className="App">
            <header className="App-header">
            <div class = 'logodrops'>
        <Link to='/' class ='link'><button class = 'imagem' style={{backgroundColor:'white'}}>
            <img src={logo} class = 'imagem' style={{backgroundColor:'white',width:110,height:100,marginLeft:30,}}></img>
          </button></Link>
        </div>
            <div class = 'contatos'>
            <div class = 'textossobre'>
          <div class= 'SobreDrops'>
          <div class = 'pai_p1'>
          <p1 class = 'p1'>A Problemática do Ensino Experimental!<p class = 'fontt'>
            <img class = 'imgs_p1' src ={FabiPensando}></img>
         </p></p1>
          </div>
          <div class = 'pai_p2'>
          <img class = 'p2' src={thal}></img>
          {this.state.texto2}
          </div>
          <div class = 'pai_p3'>
          <p3 class = 'p3'>Nossas atividades</p3>
          <img class = 'imgs_p1' src ={JessPensando}></img>
          {this.state.texto3}
          </div>
          <img class = 'book' src={deisyy}/>
          </div>
          </div>
        </div>
        <div class = 'conheca'>
          <a>Quem compõe o Drops?</a>
        </div>
        <div class = 'scene'>
          <div class ='alunos'>
          {this.state.pessoas.map(people=>
            <div class = 'todosalunos'>
            <img class = 'fotosalunos' src = {people.foto}></img>
            <div class = 'nomeal'>

              <a>{people.nome}: {people.funcao}</a>
          </div>
          </div>
            )}
          </div>
          <div class = 'alunos'>
          {this.state.pessoas2.map(people=>
            <div class = 'todosalunos'>
            <img class = 'fotosalunos' src={people.foto}></img>
            <div class = 'nomeal'>
              <a>{people.nome}: {people.funcao}</a>
          </div>
          </div>
            )}
          </div>
        </div>
            </header>
        </div>
    )
}}