import React from 'react';
import './install.css';
import Test from '../Testes/logo_an';
import {Link} from 'react-router-dom';
import img from './logo_drops.png';

export default function App(){
    return(
    <div class = 'install_app'>
        <div class = 'voltar_init'><Link to = '/'>
            <button class = 'botao_voltar_init'>
                <img class = 'img_voltar_init' src ={img}></img>
            </button></Link>
        </div>
        <div class = 'imagem_install'>
            <div class = 'img_install'><Test></Test></div>
        </div>
        <button class = 'botao_install'>
            <a class = 'a_install' href = 'https://drive.google.com/file/d/17sHcLOXIRL063b03M-3ZEWGTin-EP5Cp/view?usp=sharing'>
            Instalar o COROANADROPS APP
            </a></button>
    </div>
    )
}