import React from 'react';
import './estilo_dados.css';
import bandeira from './drops.jpeg';
import {Chart} from 'react-charts';
import { render } from 'react-dom';

export default class App extends React.Component{
    render(){
    var data = new Date();
    var dias = ['Segunda-feira','Terça-feira','Quarta-feira','Quinta-feira','Sexta-feira','Sábado','Domingo']
    return(
        <div class = 'dados'>
            <header class ='header'>
                <img class='bandeira' src = {bandeira}></img>
                <div class='nome'>CORONADROPS-PAINEL</div>
                <div class='outros'>
                    
                </div>
            </header>
            <div class = 'painel'>
                <div class = 'atualizacao'>
                    <a class = 'att_a1'>Dados da covid-19 no Brasil</a>
                    <a class = 'att_a2'>Painel da Covid-19</a>
                    <a class = 'att_a3'>Atualizado {dias[data.getDay()-1]} dia:{data.getDate()}/{data.getMonth()+1}/{data.getFullYear()} </a>
                </div>
            </div>
            <div class='namegraph'>Covid-19 em Números</div>
            <div class = 'graficos'>
               <div class='graficos1'>
               <div class ='div_titulo1'>Painel de Vacinados</div>
               <div class = 'dia'>Em {data.getDate()}/{data.getMonth()+1}/{data.getFullYear()}: </div>
                <div class = 'mes'>Semana anterior: </div>
                <div class = 'ano'>Este mês: </div>
               <span></span><span></span>
                </div>
                <div class = 'graficos2'>
                <div class ='div_titulo2'>Painel de Recuperados</div>
                <div class = 'dia'>Em {data.getDate()}/{data.getMonth()+1}/{data.getFullYear()}: </div>
                <div class = 'mes'>Semana anterior: </div>
                <div class = 'ano'>Este mês: </div>
                <span></span><span></span>
                </div>
                <div class = 'graficos3'>
                <div class ='div_titulo3'>Painel de Mortos<script></script></div>
                <div class = 'dia'>Em {data.getDate()}/{data.getMonth()+1}/{data.getFullYear()}: </div>
                <div class = 'mes'>Semana anterior: </div>
                <div class = 'ano'>Este mês: </div>
                <span></span><span></span>
                </div>
            </div>
            <div class = 'fonte'>Fonte: <a href = 'https://covid.saude.gov.br' style={{textDecoration:'none',color:'blue'}}>CORONAVIRUS BRASIL</a></div>
            <div class = 'sobre'>
                <div class = 'paginas'>
                    <a class = 'aa'>Páginas:</a>
                    <a href='/' class = 'a'>Página Inicial</a>
                    <a  href='/Distanciamento' class ='a'>Distanciamento</a>
                    <a  href='/Mascaras' class ='a'>Máscaras</a>
                    <a  href='/Sobre' class ='a'>Sobre</a>
                    <a  href='/' class ='a'>Aplicativo</a>
                    <a  href='/' class= 'a'>Redes Sociais</a>
                </div>
                <div class = 'contato'>
                <a class = 'aa'>Contato:</a>
                <a class = 'a'>CORONADROPS</a>
                <a class = 'a'>Telefone:</a>
                <a class = 'a'>Email:</a>
                <a class = 'a'>Email:</a>
                <a class = 'a'>Campina Grande-PB</a>
                <a class = 'a'>Todos os direitos</a>
                </div>
            </div>
        </div>
    )
}}
