import React from 'react';
import logo from './drops.jpeg';
import './estilo_distanciamento.css';
import Carousel from 'react-elastic-carousel';
import image_1 from './Roteiro_site.png';
import {Link} from 'react-router-dom';
import HTMLFlipBook from 'react-pageflip';


export default class App extends React.Component{
  constructor(props){
    super(props)
    this.state={
      nome:'Bem vindo ao tópico sobre Máscaras',
      color:'white',
      imagens:[{id:'Em Breve',url:''},{id:'Em Breve',url:''},{id:'Em Breve',url:''},{id:'Em Breve',url:''},]
    }
  }
  mostrar(){
    if(document.documentElement.scrollTop>100){
      this.setState({
        nome:'Você está lendo: Tipos de máscaras!',
        color:'red',})}
    else if(document.documentElement.scrollTop==0){
      this.setState({
        nome:'Bem vindo ao tópico sobre Máscaras',
        color:'white',
      })}}
  componentDidMount(){
    window.onscroll = () => this.mostrar()}
render(){
return(
    <div className="App2">
        <header className="App-header4">
        <div class = 'logodrops'>
        <Link to='/' class ='link'><button class = 'imagem'>
            <img src={logo} class = 'imagem'></img>
          </button></Link>
        </div>
        <div class = 'espacos_dist'>Distanciamento</div>
        <div class = 'espacamentos_dist'>
          <img src = {image_1} class = 'img_main_dist'></img>
          </div>
        <div class = 'banner_preto'>
        </div>
        </header>
    </div>
)}}