import React from 'react';
import './estilo_rede.css';

export default class App extends React.Component{
    constructor(props){
        super(props)
            this.state={
                publicacao:[{id:'1'},{id:'2'}]
            
        }
    }
    render(){
        const publicacao = this.state
        return(
            <div class = 'div_main'>
                <div class = 'logo_redes'>
                    <button class = 'botao_redes'>
                        <img class = 'img_redes_botao'></img>
                    </button>
                </div>
                <div class = 'titulo_redes'>Drops na Rede</div>
                <div class = 'public_redes'>
                    {this.state.publicacao.map(itens=><div class = 'postagem'>{itens.id}</div>)}
                </div>
            </div>
        );
    }
}